*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html, body {
    background: #000 url("./image/bg.jpg") no-repeat center center/cover;
    height: 100vh;
    color: #fff;
    font-family: Arial, Helvetica, sans-serif;
}

a {
    text-decoration: none;
}

.app {
    max-width: 1100px;
    margin: auto;
    padding: 0 20px;
}

header {
    height: 200px;
}

header img {
    width: 200px;
}

.center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.search {
    height: 90px;
    margin-bottom: 20px;
    max-width: 100%;
}

input[type='text']{
    display: block;
    padding: 10px 10px 10px 50px;
    font-size: 20px;
    border: 0;
    border-radius: 5px;
    width: 60%;
    margin: auto;
    // background-color: rgba(255, 255, 255, 0.2);
}

.cards {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}

.card {
    cursor: pointer;
    background-color: transparent;
    height: 300px;
    perspective: 1000px;
    margin: 10px;
}

.card h1 {
    font-size: 25px;
    border-bottom: 1px solid #fff;
    padding-bottom: 10px;
    margin-bottom: 10px;
}

.card img {
    width: 100%;
    height: 300px;
    object-fit: cover;
}

.card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    transition: transform 0.8s;
    transform-style: preserve-3d;
}

.card:hover .card-inner {
    transform: rotateY(180deg);
}

.card-front , .card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.card-back {
    background-color: #333;
    color: white;
    padding: 20px;
    transform: rotateY(180deg);
}

.card li {
    list-style: none;
}

.search img {
    position: relative;
    margin-left: 210px;
    top: 40px;
}

form {
    position: absolute;
    width: 70%;
}


@media only screen and (max-width: 600px) {

    .cards {
        display: flex;
        flex-direction: column;
    }

    .card {
        width: 100%;
        height: 500px;
        margin: 0;
        padding-bottom: 10px;
    }

    .card img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    input[type='text'] {
        width: 70%;
    }

    .search img {
        margin-top: 7px;
        margin-left: 70px;
        position: relative;
    }

    form {
        position: absolute;
        width: 90%;
    }
}

@media only screen and (min-width: 600px) and (max-width: 900px){
    .cards {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 10px;
    }

    .card {
        width: 100%;
        height: 500px;
    }

    .card img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .search img {
        display: none;
    }

    input[type='text'] {
        width: 70%;
        margin-left: 30%;
    }
}


